import {Pipe, PipeTransform} from "@angular/core";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanObjectHealthState} from "@generated/vsan-object-health-state";

@Pipe({
   name: "healthStateIconClass",
})
export class VirtualObjectHealthStateIconClassPipe implements PipeTransform {
   transform(state: VsanObjectHealthState | string): string {
      return VsanHealthUtil.getVsanObjectHealthStatusIconClass(state);
   }
}
