import {Component, Input} from "@angular/core";
import {PipeComparator} from "@component/datagrid/comparator/pipe-comparator";
import {StringComparator} from "@component/datagrid/comparator/string-comparator";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";
import {PipeFilter} from "@component/datagrid/filter/pipe-filter";
import {NavigationService} from "@navigation/navigation.service";
import {VirtualObjectHealthStateLabelPipe} from "@pipe/virtual-objects/health-state-label.pipe";
import {Icon} from "@util/icon";
import {VsanUiUtils} from "@util/vsan-util";
import {RemoteObjectData} from "./remote-object.data";

@Component({
   templateUrl: "remote-objects-table.html",
   styleUrls: ["remote-objects-table.scss"],
   selector: "vsan-remote-objects-table",
})
export class RemoteObjectsTableComponent {

   Icon: typeof Icon = Icon;

   @Input()
   remoteObjects: RemoteObjectData[];

   @Input()
   showPoliciesColumn: boolean;

   nameComparator: StringComparator<RemoteObjectData> = new StringComparator<RemoteObjectData>("name");
   accessibilityComparator: PipeComparator<RemoteObjectData> =
         new PipeComparator<RemoteObjectData>(new VirtualObjectHealthStateLabelPipe(), "healthState");
   policyComparator: StringComparator<RemoteObjectData> = new StringComparator<RemoteObjectData>("policy");
   uuidComparator: StringComparator<RemoteObjectData> = new StringComparator<RemoteObjectData>("uuid");

   nameFilter: ObjectFilter<RemoteObjectData> = new ObjectFilter<RemoteObjectData>("name");
   accessibilityFilter: PipeFilter<RemoteObjectData> =
         new PipeFilter(new VirtualObjectHealthStateLabelPipe(), "healthState");
   policyFilter: ObjectFilter<RemoteObjectData> = new ObjectFilter<RemoteObjectData>("policy");
   uuidFilter: ObjectFilter<RemoteObjectData> = new ObjectFilter<RemoteObjectData>("uuid");

   constructor(private navigationService: NavigationService) {
   }

   navigateToPolicy(policy: string): void {
      this.navigationService.navigateToVmStoragePoliciesView(policy);
   }

   get footerLabel(): string {
      if (!this.remoteObjects || !this.remoteObjects.length) {
         return VsanUiUtils.getString("vsan.na.label");
      }

      if (this.remoteObjects.length === 1) {
         return VsanUiUtils.getString("vsan.vm.monitor.diskPlacement.remoteObjectsTable.footer.singular");
      }

      return VsanUiUtils
            .getString("vsan.vm.monitor.diskPlacement.remoteObjectsTable.footer.plural", this.remoteObjects.length);
   }
}
