import {AfterViewInit, Component} from "@angular/core";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {CsdObjectService} from "@generated/csd-object-service";
import {CsdVmService} from "@generated/csd-vm-service";
import {InventoryNode} from "@generated/inventory-node";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VirtualObjectsService} from "@generated/virtual-objects-service";
import {VmCsdConfig} from "@generated/vm-csd-config";
import {VmDiskPlacementProvider} from "@generated/vm-disk-placement-provider";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {RemoteObjectData} from "./remote-objects-table/remote-object.data";

@Component({
   templateUrl: "vm-disk-placement.html",
   styleUrls: ["vm-disk-placement.scss"],
   selector: "vsan-vm-disk-placement",
})
export class VmDiskPlacementComponent implements AfterViewInit {
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;
   vmObjectsModel: VirtualObjectModel[] = [];
   remoteObjects: RemoteObjectData[];
   hasStorageClusterPermissions: boolean = true;
   error: boolean;
   vmCsdConfig: VmCsdConfig;
   vmRef: ManagedObject;
   vmObjectUuids: string[];
   objectIdentitiesSupported: boolean = false;
   hasPoliciesPrivilege: boolean = false;
   childrenVmObjectsModel: VirtualObjectModel[];

   @LoadingIndicator({name: "basic-data-loader"})
   loadingBasicData: boolean;
   @LoadingIndicator()
   loadingObjects: boolean;

   constructor(private csdVmService: CsdVmService,
         private csdObjectService: CsdObjectService,
         private virtualObjectsService: VirtualObjectsService,
         private diskPlacementProvider: VmDiskPlacementProvider,
         private navigationService: NavigationService,
         private capabilityProvider: VsanCapabilityProvider,
         private permissionService: PermissionService) {
   }

   async ngAfterViewInit(): Promise<void> {
      this.vmRef = ManagedObject.contextObject;
      await this.fetch();
   }

   @RefreshHandler
   @Loader({srMessage: "vsan.vm.monitor.diskPlacement"})
   async fetch(): Promise<void> {
      this.remoteObjects = null;

      await this.loadBasicData();

      if (this.isStretchedVm || !this.objectIdentitiesSupported || !this.hasStorageClusterPermissions) {
         return;
      }

      if (this.isRemoteVm) {
         let objectHealth: any = null;
         [objectHealth, this.vmObjectsModel] = await Promise.all([
            this.csdObjectService.getCsdObjectHealth(this.computeCluster.moRef, this.vmObjectUuids),
            this.listVmVirtualObjects(),
         ]);

         // It is just a single VM so we can use the first item from the list
         this.remoteObjects = this.vmObjectsModel[0].children.map(vmObject => new RemoteObjectData(vmObject.displayName,
                  vmObject.iconId,
                  this.storageCluster,
                  objectHealth[vmObject.uid],
                  vmObject.storagePolicy?.name,
                  vmObject.uid));
      } else {
         this.vmObjectsModel = await this.listVmVirtualObjects();
      }
      this.childrenVmObjectsModel = this.getChildrenVmObjectsModel();
   }

   @Loader({name: "basic-data-loader"})
   private async loadBasicData(): Promise<void> {
      this.vmCsdConfig = await this.csdVmService.getVmCsdConfig(this.vmRef);

      this.hasStorageClusterPermissions = this.vmCsdConfig.hasStorageClusterReadAccess;

      [this.objectIdentitiesSupported, this.vmObjectUuids, this.hasPoliciesPrivilege] = await Promise.all([
         this.capabilityProvider.getIsObjectIdentitiesSupportedOnCluster(this.storageCluster.moRef),
         this.diskPlacementProvider.getVmObjectUuids(this.vmRef),
         this.permissionService.hasVcPermissions(this.vmRef, [Privileges.READ_POLICIES()]),
      ]);
   }

   private async listVmVirtualObjects(): Promise<VirtualObjectModel[]> {
      return this.virtualObjectsService.listVmVirtualObjects(
            this.storageCluster.moRef,
            this.vmRef,
            this.vmObjectUuids);
   }

   get computeCluster(): InventoryNode {
      return this.vmCsdConfig ? this.vmCsdConfig.computeCluster : null;
   }

   get storageCluster(): InventoryNode {
      return this.vmCsdConfig && this.vmCsdConfig.storageClusters ? this.vmCsdConfig.storageClusters[0] : null;
   }

   get isRemoteVm(): boolean {
      return !!this.vmCsdConfig?.isRemote;
   }

   get isStretchedVm(): boolean {
      return this.vmCsdConfig
            && this.vmCsdConfig.storageClusters
            && this.vmCsdConfig.storageClusters.length !== 1;
   }

   private getChildrenVmObjectsModel(): VirtualObjectModel[] {
      if (VsanUiUtils.isEmpty(this.vmObjectsModel)) {
         return [];
      }
      const result: VirtualObjectModel[] = [];
      this.vmObjectsModel.forEach(objectModel => {
         if (objectModel.uid) {
            result.push(objectModel);
         }
         if (objectModel.children) {
            objectModel.children.forEach(child => {
               if (child.uid) {
                  result.push(child);
               }
            });
         }
      });
      return result;
   }

   navigateToCluster(): void {
      this.navigationService.navigateTo(PlatformExtension.LAST_VISITED_PAGE, this.storageCluster.moRef);
   }

   @ErrorHandler()
   private handleDataError(e: string): void {
      this.error = true;
   }
}
