import {PipeTransform} from "@angular/core";
import {BaseComparator} from "@component/datagrid/comparator/base-comparator";

/**
 * Extends BaseComparator by allowing the consumer to provide a pipe that will be used in the filtering process.
 */
export class PipeComparator<T> extends BaseComparator<T> {

   constructor(private pipe: PipeTransform, private dataField: string = null) {
      super();
   }

   protected compareValue(a: T, b: T): number {
      return this.pipe.transform(this.stringify(a, this.dataField)).toLowerCase()
            .localeCompare(this.pipe.transform(this.stringify(b, this.dataField)).toLowerCase());
   }
}
